  /*autocomplete clicnte */
  asyncSelected: string;
  typeaheadLoading: boolean;
  typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  /*autocomplete articulo */
  asyncSelected_art: string;
  typeaheadLoading_art: boolean;
  typeaheadNoResults_art: boolean;
  dataSource_art: Observable<any>;

  tipocontrato: any;
  estacionservicio: any;
  name = 'ng2-ckeditor';
  ckeConfig: any;
  ckeConfigdesc: any;
  ckeConfigcom: any;
  mycontent: string;
