import {
  Component,
  OnInit,
  AfterViewInit,
  ViewChild,
  OnDestroy,
} from "@angular/core";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { NotificationService, LocalDataService } from "./../../shared";
import { IncidenciaService } from "./../incidencia.service";
import * as $ from "jquery";

import "datatables.net";
import "datatables.net-bs4";
import "datatables.net-buttons";
import { UsuariosService } from "../../usuarios/usuarios.service";
import { ESTADOINCIDENCIAS } from "../../mocks/estadoincidente";
import { BsLocaleService } from "ngx-bootstrap/datepicker";
import * as moment from "moment";
import { isNullOrUndefined } from "util";
import { OptionsInput } from "fullcalendar";
import { CalendarComponent } from "ng-fullcalendar";
//import { Options } from 'fullcalendar';

import { Router } from "@angular/router";
import { Incidencia } from "../../viewmodel/incidencia";
import { SwalComponent } from "@toverux/ngx-sweetalert2";
// import { } from '@types/googlemaps';
import { EstacionservicioService } from "../../estacionservicio/estacionservicio.service";
import { MapsService } from "../../google-apis/maps.service";
import { Subscription } from "rxjs";
import { timer } from "rxjs/observable/timer";
@Component({
  selector: "app-incidencia-index",
  templateUrl: "./incidencia-index.component.html",
  styleUrls: ["./incidencia-index.component.css"],
})
export class IncidenciaIndexComponent
  implements OnInit, AfterViewInit, OnDestroy
{
  table = null;
  usuarios = [];
  incidencias = [];
  usuariostec = [];
  estadoincidente = ESTADOINCIDENCIAS;
  mytime: Date = new Date();
  ususel: any;
  estadosel: any;
  mindate: any;
  calendarOptions: OptionsInput;
  displayEvent: any;
  verplaning = false;
  vermaps = true;
  verlista = true;
  incnew: Incidencia;
  @ViewChild("gmap") gmapElement: any;
  map: google.maps.Map;
  @ViewChild(CalendarComponent) ucCalendar: CalendarComponent;
  @ViewChild("createSwal") private createSwal: SwalComponent;
  @ViewChild("filtroSwal") private filtroSwal: SwalComponent;
  @ViewChild("confirmplaneacionSwal")
  private confirmplaneacionSwal: SwalComponent;
  @ViewChild("assignInbox") private assignInbox: SwalComponent;
  fechas: any;
  lat = 40.50017;
  long = -3.89239;
  markers = [];
  estaciones: any;
  geolocationPosition: any;
  istec = false;
  selected_all = true;
  usuario: any;
  order_values = [
    { name: "Vencidos", estado: "asc" },
    { name: "Cliente", estado: "asc" },
    { name: "Fecha registro", estado: "asc" },
    { name: "Fecha Planeado", estado: "asc" },
  ];
  ordername: string;
  orderestado: string;
  f_fecharegistro: string;
  f_fechacreacion: string;
  f_cliente: string;
  f_estacion: string;
  f_itulo: string;
  f_incidente: string;
  alive = true;
  subscription: Subscription;
  estacionesfilter: any;
  mensajePlaneacion: string;

  incidenceDetail: boolean = false;
  incidenceId: number;

  inboxusertec: any;
  selectedInboxInc : any = null;
  currentProfile;
  tecnicoWhiteList=["PLA","PDA","REC","TRA"]
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private incidenciaService: IncidenciaService,
    private usuariosService: UsuariosService,
    private notificationService: NotificationService,
    private localeService: BsLocaleService,
    private localdataservice: LocalDataService,
    private estacionservicioService: EstacionservicioService,
    private mapsService: MapsService,
    private router: Router
  ) {
    this.localeService.use("es");
    this.estadosel = "";
    this.ususel = "";
    this.mindate = new Date();
    this.istec = this.localdataservice.getuser().perfil == 1;
    this.usuario = this.localdataservice.getuser();
    this.ususel = "";
    this.f_cliente = "";
    this.f_estacion = "";
    this.f_fechacreacion = "";
    this.f_fecharegistro = "";
    this.f_itulo = "";
    this.orderestado = "";
    this.ordername = "";
    this.f_incidente = "";
    //this.subscribeToData();
    this.currentProfile=this.localdataservice.getuser().perfil*1;
  }

  ngOnInit() {
    this.spinnerService.show();
    this.getdata();
    this.initcalendar();
    this.incnew = new Incidencia();
    this.vermaps = false;
  }
  /*private subscribeToData(): void {
    timer(0, 40000)
      .takeWhile(() => this.alive) // only fires when component is alive
      .subscribe(() => {
        this.getdata(false);
      });
  }*/
  initcalendar() {
    const dateObj = new Date();
    const yearMonth =
      dateObj.getUTCFullYear() + "-" + (dateObj.getUTCMonth() + 1);

    this.calendarOptions = {
      editable: true,
      eventLimit: false,
      navLinks: true,
      selectable: true,
      locale: "es",

      header: {
        left: "prev,next today",
        center: "title",
        right: "month,agendaWeek,agendaDay,listMonth",
      },
      events: [],
      allDayText: "Todo el dia",
      buttonText: {
        today: "Hoy",
        month: "Mes",
        week: "Semana",
        day: "Dia",
        list: "Agenda",
      },
    };
  }
  eventallow() {
    alert("allow");
  }
  getdata(hideList: boolean = true) {
    if (hideList) {
      this.verlista = false;
    }

    if (hideList) {
      this.incidenceId = -1;
    }
    this.spinnerService.show();

    if (this.localdataservice.getuser().perfil == "1") {
      this.ususel = this.localdataservice.getuser().id;
    }

    if(this.selected_all) {
      this.ususel = "";
    }
    this.incidenciaService
      .getbytoken(
        "estado=" +
          this.estadosel +
          "&usu=" +
          this.ususel +
          "&filtro=" +
          this.ordername +
          "&tipoorden=" +
          this.orderestado +
          "&f_fecharegistro=" +
          this.f_fecharegistro +
          "&f_fechacreacion=" +
          this.f_fechacreacion +
          "&f_cliente=" +
          this.f_cliente +
          "&f_estacion=" +
          this.f_estacion +
          "&f_itulo=" +
          this.f_itulo +
          "&f_incidente=" +
          this.f_incidente
      )
      .subscribe((response) => {
        this.usuarios =
          isNullOrUndefined(this.usuarios) || this.usuarios.length == 0
            ? response.data.usuarios
            : this.usuarios;

        this.usuarios = this.usuarios.sort((a, b) => a.name > b.name ? 1 : -1)
        this.incidencias = response.data.incidencias;
        if(this.currentProfile===1){ //teccnico
          //INCIDENCIAS QUE NO SEA INT 
          this.incidencias=this.incidencias.filter((inc)=>{
            return this.tecnicoWhiteList.includes(inc.estado);
          })
        }

        if(this.ususel){
          this.incidencias=this.incidencias.filter((it)=>{
            const valid=it && it.usuario_inbox;
            return valid && (it.usuario_inbox.id=== this.ususel);
          });
        }

        this.verlista = !!this.incidencias.length;
        this.usuariostec = response.data.usuariostec;
        this.estacionesfilter = response.data.estaciones;
        this.spinnerService.hide();
        if(this.selected_all) this.ususel = -1;

      });
  }
  loadtable() {
    let exampleId: any = $("#example23");

    this.table = exampleId.DataTable({
      dom: "Bfrtip",
      buttons: ["copy", "csv", "excel", "pdf", "print"],
      ordering: false,
      oLanguage: {
        sLengthMenu: "_MENU_ registros por pgina",
        sInfoFiltered: " - filtrada de _MAX_ registros",
        sSearch: "Buscar: ",
        sZeroRecords: "No existen datos diponibles",
        sInfoEmpty: " ",
        sInfo: "Mostrando _END_ de _TOTAL_",
        oPaginate: {
          sPrevious: "Anterior",
          sNext: "Siguiente",
        },
      },
    });
  }
  private reInitDatatable(): void {
    if (this.table) {
      this.table.destroy();
      this.table = null;
    }
    setTimeout(() => this.loadtable(), 0);
  }
  actualizarincidenye(inc) {
    let fectem = inc.fecha_programacion;
    if (
      inc.fecha_programacion != undefined &&
      inc.fecha_programacion != null &&
      inc.fecha_programacion != ""
    ) {
      if (
        inc.hora_programacion != undefined &&
        inc.hora_programacion != null &&
        inc.hora_programacion != ""
      ) {
        if (!moment(inc.fecha_programacion, "DD/MM/YYYY").isValid()) {
          inc.fecha_programacion = moment(inc.fecha_programacion).format(
            "YYYY-MM-DD"
          );
        } else {
          inc.fecha_programacion = moment(
            inc.fecha_programacion,
            "DD/MM/YYYY"
          ).format("YYYY-MM-DD");
        }
        inc.fecha_programacion = moment(
          inc.fecha_programacion + " " + inc.hora_programacion
        ).format("DD-MM-YYYY HH:mm:ss");
      } else {
        if (!moment(inc.fecha_programacion, "DD/MM/YYYY").isValid()) {
          inc.fecha_programacion = moment(inc.fecha_programacion).format(
            "YYYY-MM-DD"
          );
        } else {
          inc.fecha_programacion = moment(
            inc.fecha_programacion,
            "DD/MM/YYYY"
          ).format("YYYY-MM-DD");
        }
        inc.fecha_programacion = moment(
          inc.fecha_programacion + " " + "00:00:00"
        ).format("DD-MM-YYYY HH:mm:ss");
      }
    }

    //inc.ignore_user = 1;

    this.incidenciaService.update(inc).subscribe(
      (value) => {
        if (value.data.noplaneado == 1) {
          this.notificationService.onError(
            "Ya tiene asignado un incidente en esa hora."
          );
        } else if (value.data.errorusu == 1) {
          this.notificationService.onError(
            "No se puede asignar el usuario por que es tecnico adicional"
          );
        } else {
          if (value.data.errorrec == undefined) {
            inc.usuario_asignado = value.data.usuario_asignado;
            inc.remotoinicio = value.data.remotoinicio;
            inc.remotopausa = value.data.remotopausa;
            inc.estado = value.data.estado;
            this.incnew = null;
            this.spinnerService.hide();
            this.notificationService.onSuccess("Incidente Actualizado.");
          } else {
            inc.estado = "PLA";
            this.notificationService.onError(
              "Ya esta trabajando en un incidente"
            );
          }
        }
      },
      (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("Incidente no Actualizado.");
        } else {
          this.notificationService.onError("Incidente no Actualizado.");
        }
      }
    );
    inc.fecha_programacion = fectem;
  }
  validaAsignacionUsuario(inc) {
    this.incnew = inc;
    let vencimiento, planeado, diferencia;
    vencimiento = moment(inc.fecha_vencimiento, "DD-MM-YYYY HH:mm:ss");
    planeado = moment(inc.fecha_programacion, "DD-MM-YYYY HH:mm:ss");
    diferencia = planeado.diff(vencimiento);
    console.log("diferencia" + diferencia);
    if (diferencia < 0) {
      this.mensajePlaneacion =
        "Desea planear la incidencia?. <br>La fecha de planeacin esta fuera de plazo.";
    } else {
      this.mensajePlaneacion = "Desea planear la incidencia?.";
    }

    this.confirmplaneacionSwal.show();
  }
  onasignarusuario() {
    this.incnew.estado = "PLA";
    this.actualizarincidenye(this.incnew);
  }
  onreiniciarincidente(inc) {
    inc.estado = "INT";
    inc.usuario_asignado_id = null;
    this.actualizarincidenye(inc);
  }

  onskipincidente(inc) {
    inc.estado = "PLA";

    this.actualizarincidenye(inc);
  }
  getclassestadoincidencia(estado) {
    let result;
    result = this.estadoincidente.filter(
      (estadoinc) => estadoinc.value === estado
    );
    return result.length > 0 ? result[0].label : "";
  }
  ngAfterViewInit() {
    $.getScript("assets/dist/js/pages/clockpicker.js");
  }
  adclock(inc) {
    if (!this.istec) {
      inc.isEditable = true;
      if (
        inc.fecha_programacion != undefined &&
        inc.fecha_programacion != null &&
        inc.fecha_programacion != ""
      ) {
        inc.fecha_programacion = moment(inc.fecha_programacion).format(
          "DD/MM/YYYY h:mm:ss"
        );
      }
      $.getScript("assets/dist/js/pages/clockpicker.js");
    }
  }

/**
 * Select the user and get their incidences
 * @param usuel - selected user
 */
  selUsu(usuel) {
    this.spinnerService.show();
    this.resetFilter();
    this.selected_all = false;
    
    for (let usr of this.usuarios) {
      usr.selected = false;
    }

    this.ususel = usuel.id;
    usuel.selected = true;
    this.getdata();
  }

  /**
   * Select all incidences
   */
  selAll() {
    this.spinnerService.show();

    this.resetFilter();
    this.selected_all = true;

    for (let usr of this.usuarios) {
      usr.selected = false;
    }

    this.getdata();
  }

  /**
   * It resets the values of the filter variables to empty strings.
   */
  resetFilter() {
    this.estadosel = "";
    this.ususel = "";
    this.ordername = "";
    this.orderestado = "";
    this.f_fecharegistro = "";
    this.f_fechacreacion = "";
    this.f_cliente = "";
    this.f_estacion = "";
    this.f_itulo = "";
    this.f_incidente = "";
  }

  selEstado(selestado) {
    for (let estado of this.estadoincidente) {
      estado.selected = false;
    }

    this.estadosel = selestado.value;
    this.spinnerService.show();
    selestado.selected = true;
    this.getdata();
    return false;
  }
  removerfiltros() {
    for (let estado of this.estadoincidente) {
      estado.selected = false;
    }
    for (let usr of this.usuarios) {
      usr.selected = false;
    }
    this.selected_all = false;
    this.resetFilter();
    this.spinnerService.show();
    this.getdata();
  }
  clickButton(model: any) {
    console.log(model);
  }
  eventClick(model: any) {
    if (model.event.estado == "CLO") {
      this.router.navigate(["/incidencia/resumen", model.event.id]);
    } else {
      this.router.navigate(["/incidencia/edit", model.event.id]);
    }
  }
  select(start) {
    if (start.view.type != "agendaDay") {
      this.ucCalendar.fullCalendar(
        "changeView",
        "agendaDay",
        start.date.format("YYYY-MM-DD")
      );
      this.ucCalendar.fullCalendar("gotoDate", start.date);
    } else if (start.view.type == "agendaDay") {
      if (this.incnew.id != undefined) {
        this.incnew.fecha_programacion = start.date;
        this.incnew.hora_programacion = start.date.format("h:mm:ss");
        this.incnew.start = start.date.format("DD/MM/YYYY h:mm:ss a");
        this.createSwal.show();
      }
    }
  }
  selectDAY(start) {
    this.ucCalendar.fullCalendar(
      "changeView",
      "agendaDay",
      start.date.format("YYYY-MM-DD")
    );
    this.ucCalendar.fullCalendar("gotoDate", start.date);
  }
  updateEvent(model: any) {
    model.event.fecha_programacion = model.event.start;
    this.actualizarincidenye(model.event);
  }
  verplanning() {
    this.getdata();
    this.verlista = false;
    this.verplaning = true;
    this.vermaps = false;
  }
  verlistamenu() {
    this.verlista = true;
    this.verplaning = false;
    this.vermaps = false;
  }
  verlistmaps() {
    this.verlista = false;
    this.verplaning = false;
    this.vermaps = true;
    let myLatlng = new google.maps.LatLng(this.lat, this.long);

    let mapProp = {
      center: myLatlng,
      zoom: 5,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
    };
    this.map = new google.maps.Map(this.gmapElement.nativeElement, mapProp);
    this.getEstacionservicio();
  }

  eventRender(event) {
    event.element
      .find(".fc-title, .fc-list-item-title")
      .html("<b>" + event.event.title + "</b>");
  }
  drop(event) {
    if (event.event.estado != "CLO") {
      let inc = new Incidencia();
      inc.id = event.event.id;
      inc.categoria_incidente_id = event.event.id;
      inc.cliente_id = event.event.cliente_id;
      inc.contrato_componente_id = event.event.contrato_componente_id;
      inc.descripcion = event.event.descripcion;
      inc.empresa_id = event.event.empresa_id;
      inc.estacion_servicio_id = event.event.estacion_servicio_id;
      inc.fecha_cierre = event.event.fecha_cierre;
      inc.fecha_programacion = moment(event.event.start).format("YYYY-MM-DD");
      inc.fecha_respuesta = event.event.fecha_respuesta;
      inc.fecha_vencimiento = event.event.fecha_vencimiento;
      inc.hora_programacion = moment(event.event.start).format("HH:mm:ss");
      inc.razonsocial = event.event.razonsocial;
      inc.titulo = event.event.titulo;
      inc.tipocontrato = event.event.tipocontrato;
      inc.usuario_asignado = event.event.usuario_asignado;
      inc.id = event.event.id;

      this.actualizarincidenye(inc);
    } else {
      return false;
    }
  }

  verencalendario(inc) {
    if (!this.istec) {
      this.verplanning();

      this.incnew = inc;
    }
  }
  getEstacionservicio() {
    this.estacionservicioService.get(-1).subscribe((response) => {
      this.estaciones = response.data;
      let myLatlng = new google.maps.LatLng(this.lat, this.long);
      for (let i = 0; i < this.estaciones.length; i++) {
        let est = this.estaciones[i];
        myLatlng = new google.maps.LatLng(est.lat, est.lng);
        let marker = new google.maps.Marker({
          position: myLatlng,
          map: this.map,
          animation: google.maps.Animation.DROP,
          title: "Hola!",
          label: est.name.substring(0, 1),
        });
        let contentString =
          '<div class="info-window-content"><h2>' +
          est.name +
          "</h2>" +
          "<p> " +
          est.direccion +
          "<br> " +
          est.telefonos +
          "</p></div>";

        let infowindow = new google.maps.InfoWindow({
          content: contentString,
        });

        google.maps.event.addListener(marker, "click", function () {
          infowindow.open(this.map, marker);
        });
      }
      this.map.setCenter(myLatlng);
      this.spinnerService.hide();
    });
  }
  updateestado(inc, remoto, remotopausa) {
    inc.remoto = remoto;
    inc.remotopausa = remotopausa;
    if (inc.remoto) {
      inc.estado = "TRA";
      this.actualizarincidenye(inc);
    } else {
      this.gpsuser(inc);
    }
  }

  gpsuser(inc) {
    this.spinnerService.show();
    if (navigator && navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          this.geolocationPosition = position;
          this.spinnerService.hide();
          if (inc.estado == "PLA" || inc.estado == "PDA") {
            inc.estado = "REC";
            inc.latpartida = this.geolocationPosition.coords.latitude;
            inc.lngpartida = this.geolocationPosition.coords.longitude;
            this.mapsService
              .getdistance(
                inc.latpartida,
                inc.lngpartida,
                inc.estacion_servicio.lat,
                inc.estacion_servicio.lng
              )
              .subscribe((response) => {
                console.log(response);
                if (
                  response.rows.length > 0 &&
                  response.rows[0].elements[0].duration != undefined
                ) {
                  inc.tiempo = response.rows[0].elements[0].duration.value;
                  inc.distanciacalculada =
                    response.rows[0].elements[0].distance.value;
                }

                this.actualizarincidenye(inc);
              });
          } else if (inc.estado == "REC") {
            inc.estado = "TRA";
            inc.latllegada = this.geolocationPosition.coords.latitude;
            inc.lngllegada = this.geolocationPosition.coords.longitude;
            this.actualizarincidenye(inc);
          }
        },
        (error) => {
          switch (error.code) {
            case 1:
              alert("Permission Denied To Gps");
              break;
            case 2:
              alert("Position Unavailable To Gps");
              break;
            case 3:
              alert("Timeout To Gps");
              break;
          }
        }
      );
    }
  }
  ordering(i) {
    let value = this.order_values[i];
    this.ordername = value.name;
    this.orderestado = value.estado;
    this.getdata();
    if (value.estado == "asc") {
      this.order_values[i].estado = "desc";
    } else {
      this.order_values[i].estado = "asc";
    }
  }
  openmorefilters() {
    this.filtroSwal.show();
  }
  ngOnDestroy() {
    this.alive = false; // switches your IntervalObservable off
  }
  filterestacion(estacion) {
    this.f_estacion = estacion;
    this.getdata();
  }
  reabrir($id) {
    this.spinnerService.show();
    this.incidenciaService.getReopenById($id).subscribe(
      (value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess("Incidente Actualizado.");
        this.getdata();
      },
      (err) => {
        this.spinnerService.hide();
        if (err.status_code == 400) {
          this.notificationService.onError(err.errors.message[0]);
        } else {
          this.notificationService.onError("Incidente no Actualizado.");
        }
      }
    );
  }

  // ===================================================================================
  onGetIncidenceById(id: number): void {
    this.incidenceDetail = false;

    this.incidenceId = id;
  }

  showIncidenceDetail(show) {
    this.incidenceDetail = show;
  }

  selectedUserInbox( inc ){
    this.selectedInboxInc = inc;
    this.assignInbox.show();
  }

  onAssignInbox(){
    this.incidenciaService.inboxUser( this.incidenceId , this.inboxusertec ).subscribe(
      (value) => {
        this.notificationService.onSuccess("Tcnico Actualizado.");
        this.getdata();
      },
      (err) => {
        this.notificationService.onError(err.errors.message[0]);
        this.notificationService.onError("Tcnico no Actualizado.");
      }
    )

  }

  
}



