import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IncidenciaIndexComponent } from './incidencia-index/incidencia-index.component';
import { IncidenciaAddComponent } from './incidencia-add/incidencia-add.component';
import { IncidenciaEditComponent } from './incidencia-edit/incidencia-edit.component';
import { IncidenciasResumenComponent } from './incidencias-resumen/incidencias-resumen.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';

const routes: Routes = [{ path: '', component: IncidenciaIndexComponent },
{ path: 'add', component: IncidenciaAddComponent, canDeactivate: [Candeactivateguard] },
{ path: 'edit/:id', component: IncidenciaEditComponent, canDeactivate: [Candeactivateguard] },
{ path: 'resumen/:id', component: IncidenciasResumenComponent, canDeactivate: [Candeactivateguard] },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IncidenciasRoutingModule { }
 


