import { Component, OnInit, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { IncidenciaService } from "./../incidencia.service";
import { Ng4LoadingSpinnerService } from "ng4-loading-spinner";
import { NotificationService } from "./../../shared";
import { LocalDataService } from "./../../shared";
import { tipocomponente } from "../../viewmodel/tipocomponente";
import { Contrato } from "../../viewmodel/contrato";
import { Observable } from "rxjs";
import { ClienteService } from "../../clientes/cliente.service";
import { TypeaheadMatch } from "ngx-bootstrap/typeahead";
import { listLocales } from "ngx-bootstrap/chronos";
import { BsDatepickerConfig, BsLocaleService } from "ngx-bootstrap/datepicker";
import { mergeMap } from "rxjs/operators";
import { TipocontratoService } from "../../tipocontrato/tipocontrato.service";
import { EstacionservicioService } from "../../estacionservicio/estacionservicio.service";
import { estacionservicio } from "../../viewmodel/estacionservicio";
import { tipocontrato } from "../../viewmodel/tipocontrato";
import { SwalComponent } from "@toverux/ngx-sweetalert2";
import { Componente } from "../../viewmodel/Componente";
import { ComponenteActuacion } from "../../viewmodel/componente_actuacion";
import { Incidencia } from "../../viewmodel/incidencia";
import { CategoriaIncidente } from "../../viewmodel/categoriaincidente";
import { Candeactivateguard } from "../../shared/can-deactivate-guard/can-deactivate-guard.service";
import { environment } from "../../../environments/environment";
import { ProyectosService } from "../../proyectos/proyectos.service";
import { Proyect } from "@app/viewmodel/proyect";

@Component({
  selector: "app-incidencia-add",
  templateUrl: "./incidencia-add.component.html",
  styleUrls: ["./incidencia-add.component.css"],
})
export class IncidenciaAddComponent implements OnInit {
  item: Incidencia;
  asyncSelected: string;
  typeaheadLoading: boolean;
  typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  tipocontrato: any;
  estacionservicio: any;
  name = "ng2-ckeditor";
  ckeConfig: any;
  mycontent: string;
  newcompo: estacionservicio;
  tipocontratosel: tipocontrato;
  componentesel: Componente;
  actuacionesNew: ComponenteActuacion;
  prioridades: CategoriaIncidente[];
  @ViewChild("myckeditor") ckeditor: any;
  @ViewChild("createSwal") private createSwal: SwalComponent;
  @ViewChild("confirmplaneacionSwal")
  private confirmplaneacionSwal: SwalComponent;
  bloqueo = false;
  selectauto = false;
  observacionescontrato = "";
  mensajePlaneacion: string;
  apiurl: any;
  @ViewChild("ctrlform") ctrlform: any;
  validform: boolean;

  proyects: Array<any>;

  selectedProyectId: any;

  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private incidenciaService: IncidenciaService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private clienteService: ClienteService,
    private localeService: BsLocaleService,
    private tipocontratoService: TipocontratoService,
    private estacionservicioService: EstacionservicioService,
    private candeactivateguard: Candeactivateguard,
    private proyectosService: ProyectosService
  ) {
    this.candeactivateguard.setedit(true);
    this.localeService.use("es");

    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.item.razonsocial).subscribe((value) => {
        observer.next(value.data);
      });
    });
    this.apiurl = environment.apiUrl;
  }

  ngOnInit() {
    this.validform = false;
    this.ckeConfig = {
      allowedContent: false,
      extraPlugins: "divarea",
      forcePasteAsPlainText: true,
    };
    this.item = new Incidencia();
    this.item.cliente_id = "";
    this.item.estacion_servicio_id = -1;
    this.item.categoria_incidente_id = -1;
    this.item.actuacion = "-1";
    this.item.proyecto_id = "-1";
    this.cargarTipoCOntrato();
    this.cargarEstacionServicio();
    this.cargarProyectos();
    this.newcompo = new estacionservicio();
    this.prioridades = [];
    /*  this.ctrlform.form.valueChanges.subscribe(data => {
       console.log("suscribed", this.ctrlform.form.get("ctrlproyecto"));
       this.item.proyecto_id = this.ctrlform.form.get("ctrlproyecto").value;
       this.ctrlform.form.controls["ctrlproyecto"].updateValueAndValidity(); // update value and validity.
     }); */
  }

  onChange($event: any): void {
    console.log("onChange");
  }
  cargarTipoCOntrato() {
    this.tipocontratoService.get(1).subscribe((value) => {
      this.tipocontrato = value.data;
    });
  }

  async cargarEstacionServicio() {
    let empresa = 0;
    this.spinnerService.show();
    empresa = this.localDataService.getLocalData("empresaSeleccionada").id;
    let subEst = this.estacionservicioService
      .getactbycliente(empresa, this.item.cliente_id)
      .toPromise();

    let res = await Promise.all([subEst]);
    let prEst = res[0];
    if (prEst != undefined) {
      this.estacionservicio = prEst.data;
      this.spinnerService.hide();
    }
  }

  async onselectEstacionServicio(estacionserviciosel) {
    this.item.razonsocial = "";
    this.item.cliente_id = "";
    console.log({estacionserviciosel})
    if (estacionserviciosel.id != "-1") {
      let busRazon: boolean = false;
      if (
        (this.item.razonsocial == "" || this.item.razonsocial == undefined) &&
        (this.item.cliente_id == undefined || this.item.cliente_id == "")
      ) {
        busRazon = true;
      }

      let result = this.estacionservicio.filter(
        (comp) => comp.id.toString() == estacionserviciosel.id
      );
      if (result.length > 0) {
        this.item.cliente_id = result[0].codigo_cliente;
        if (result[0].componentes != null) {
          if (result[0].componentes.length == 0) {
            await this.cargarEstacionServicio();
            result = this.estacionservicio.filter(
              (comp) => comp.id.toString() == estacionserviciosel.id
            );
          }
        }
        this.cargarProyectos();
        this.newcompo = result[0];
      }
      console.log({busRazon})
      if (busRazon) {
        this.spinnerService.show();
        this.clienteService
          .gettoken(this.newcompo.razon_social)
          .subscribe((value) => {
            if (value.data.length > 0) {
              if (value.data[0].bloqueado != "N") {
                this.bloqueo = true;
              } else {
                this.bloqueo = false;
              }
              this.item.razonsocial = this.newcompo.razon_social;
              this.item.cliente_id = this.newcompo.codigo_cliente;
            }
           // this.spinnerService.hide();
          },e=>{
            console.log("Error",e)
          },()=>{
            console.log("finally")
            this.spinnerService.hide();

          });
      }
    } else {
      this.newcompo = new estacionservicio();
    }
  }

  onselecttipocontrato(tipocontratovalue) {
    let result;
    result = this.tipocontrato.filter(
      (comp) => comp.id.toString() == tipocontratovalue.target.value
    );

    if (result.length > 0) {
      this.tipocontratosel = result[0];
    }
  }
  validaradd() {
    this.validform = false;
    if (this.ctrlform.form.valid == false) {
      this.validform = true;
      return false;
    }

    if (this.item.proyecto_id == "-1") {
      this.validform = true;
      return false;
    }

    if (this.item.contrato_componente_id == undefined) {
      this.mensajePlaneacion = "Desea guardar el incidente sin contrato?";
    } else {
      this.mensajePlaneacion = "Desea guardar el incidente?";
    }
    this.confirmplaneacionSwal.show();
  }
  add() {
    this.spinnerService.show();
    this.candeactivateguard.setedit(false);
    this.item.empresa_id = this.localDataService.getLocalData(
      "empresaSeleccionada"
    ).id;
    this.item.usuario_id = this.localDataService.getLocalData("userInfo").id;
    this.item.estacion_servicio_id = this.newcompo.id;
    // Console
    console.log(this.item);
    this.incidenciaService.add(this.item).subscribe(
      (value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess("Incidencia agregada con exito.");
        this.router.navigateByUrl("incidencia");
      },
      (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError("El Contrato ya existe");
        } else if (err.status_code == 400) {
          this.notificationService.onError(err.errors.message[0]);
        } else {
          this.notificationService.onError("Se ha producido un error.");
        }
      }
    );
  }
  changeTypeaheadLoading(e: boolean): void {
    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.item.cliente_id = e.item.codigo_cliente;
    if (e.item.bloqueado != "N") {
      this.bloqueo = true;
    } else {
      this.bloqueo = false;
    }

    this.cargarEstacionServicio();
    this.cargarProyectos();
  }

  cargarProyectos() {
    this.proyectosService.getproybycliente(this.item.cliente_id).subscribe(
      (res) => {
        console.log(res.data, "proyectos obtenidos");
        this.proyects = this.formatProjects(res.data);
      },
      (err) => {}
    );
  }

  /**
   * It takes an array of objects, and returns a new array of objects, where each object has a new
   * property called temp_name, which is a concatenation of two other properties.
   * @param data - the data that is being passed to the select
   * @returns An array of objects with the same structure as the original data, but with an additional
   * property called temp_name.
   */
  formatProjects(data) {
    return data.map((item) => {
      return {
        ...data,
        temp_name: item.codigo_proyecto + " | " + item.descripcion,
      };
    });
  }

  onChangeProyect(selectedProyect) {
    if (selectedProyect) {
      /* console.log("Proyect exist", this.ctrlform.form.value, this.ctrlform)
      let selectedProyect: Proyect[] = this.proyects.filter(proyect_id => this.ctrlform.form.value.ctrlproyecto.toString() == proyect_id.id);
      console.log(selectedProyect, "selectedProyect");
      console.log(proyect, "proyect");
      this.ctrlform.form.controls['ctrlproyecto'].setValue(selectedProyect)
      this.item.proyecto_id = selectedProyect[0].proyecto_id;
      this.item.proyecto_description = selectedProyect[0].proyecto_description; */
      console.log(selectedProyect, "selectedProyect value");
      //this.item.proyecto_id = proyect.item.proyeto_id;
      //console.log( this.ctrlform.form.controls['ctrlproyecto'].value, this.ctrlform.form.get("ctrlproyecto"), "ctrlproyecto");
      //this.item.proyecto_id = proyect.target.value;

      console.log(selectedProyect.temp_name.split(" | ")[0]);

      let selectedProyectId: Proyect[] = Object.values(
        selectedProyect as Record<string, any>[]
      ).filter(
        (item) =>
          item.codigo_proyecto === selectedProyect.temp_name.split(" | ")[0]
      );

      console.log(selectedProyectId, "after filter");

      /* this.item.proyecto_id = selectedProyectId[0].codigo_proyecto;
      this.item.proyecto_description = selectedProyectId[0].descripcion; */

      // this.item.proyecto_id = null;
      this.item.proyecto_id = selectedProyectId[0].codigo_proyecto;

      console.log(selectedProyectId, "selectedProyect value");
      console.log(this.item, "item");
    } else {
      console.log("Proyect do not exist");
      this.item.proyecto_id = "-1";
    }
  }

  selectComponent(comp) {
    if (comp.select) {
      comp.select = false;
    } else {
      comp.select = true;
    }
    return false;
  }
  modalactuacion(item) {
    this.componentesel = item;
    if (!this.componentesel.actuaciones) {
      this.componentesel.actuaciones = [];
    }

    this.createSwal.show();
    return false;
  }
  agregaractuacion() {
    this.componentesel.actuaciones.push(this.actuacionesNew);
    this.actuacionesNew = new ComponenteActuacion();
    this.actuacionesNew.tipo = "-1";
  }
  onvaluechanged(value) {
    if (value.index === undefined || value.index === "") {
      this.actuacionesNew.dias = value.dias;
    } else {
      this.componentesel.actuaciones[value.index].dias = value.dias;
    }
  }
  clickcontrato(contrato, row) {
    this.quitarobservaciones();
    this.prioridades = contrato.contrato.tipocontrato.categoriaincidentes;
    this.item.contrato_componente_id = contrato.id;
    row.observacionescontrato = contrato.contrato.observaciones;
    row.archivos = contrato.contrato.archivos;
  }
  quitarobservaciones() {
    this.newcompo.componentes.forEach((element) => {
      element.observacionescontrato = "";
      element.archivos = [];
    });
  }
  clicksincontrato(id) {
    this.prioridades = [];
    this.item.sincontrato = id;
    this.item.contrato_componente_id = null;
  }
  essiniestro(event) {
    this.item.siniestro = event.target.checked;
  }
}



