import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { IncidenciaService } from './../incidencia.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { NotificationService } from './../../shared';
import { LocalDataService } from './../../shared';
import { tipocomponente } from '../../viewmodel/tipocomponente';
import { Contrato } from '../../viewmodel/contrato';
import { Observable } from 'rxjs';
import { ClienteService } from '../../clientes/cliente.service';
import { TypeaheadMatch } from 'ngx-bootstrap/typeahead';
import { listLocales } from 'ngx-bootstrap/chronos';
import { BsDatepickerConfig, BsLocaleService } from 'ngx-bootstrap/datepicker';
import { mergeMap } from 'rxjs/operators';
import { TipocontratoService } from '../../tipocontrato/tipocontrato.service';
import { EstacionservicioService } from '../../estacionservicio/estacionservicio.service';
import { estacionservicio } from '../../viewmodel/estacionservicio';
import { tipocontrato } from '../../viewmodel/tipocontrato';
import { SwalComponent } from '@toverux/ngx-sweetalert2';
import { Componente } from '../../viewmodel/Componente';
import { ComponenteActuacion } from '../../viewmodel/componente_actuacion';
import { Incidencia } from '../../viewmodel/incidencia';
import { CategoriaIncidente } from '../../viewmodel/categoriaincidente';
import { FileUploader, FileLikeObject, FileItem, ParsedResponseHeaders } from 'ng2-file-upload';
import { environment } from '../../../environments/environment';
import * as moment from 'moment';
import { ESTADOINCIDENCIAS } from '../../mocks/estadoincidente';
import { Respuesta } from '../../viewmodel/respuesta';
import { RespuestasService } from '../../respuesta/respuestas.service';
import { ArticuloService } from '../../articulo/articulo.service';
import { MaterialRespuesta } from '../../viewmodel/MaterialRespuesta';
import { ComentarioIncidente } from '../../viewmodel/comentario_incidente';
import { ComentarioincidenteService } from '../../comentarioincidente/comentarioincidente.service';
import { Tecnicoadicional } from '../../viewmodel/tecnicoadicional';
declare var $;

@Component({
  selector: 'app-incidencias-resumen',
  templateUrl: './incidencias-resumen.component.html',
  styleUrls: ['./incidencias-resumen.component.css']
})
export class IncidenciasResumenComponent implements OnInit, AfterViewInit {

  public uploader: FileUploader = new FileUploader({
    url: environment.apiUrl + 'api/archivoincidencia',
    authTokenHeader: "Authorization",
    authToken: "Bearer " + localStorage.getItem('access_token'),
    isHTML5: true,
    autoUpload: true,
    maxFileSize: 5 * 1024 * 1024,

  });

  item: Incidencia;
  /*autocomplete clicnte */
  asyncSelected: string;
  typeaheadLoading: boolean;
  typeaheadNoResults: boolean;
  dataSource: Observable<any>;
  /*autocomplete articulo */
  asyncSelected_art: string;
  typeaheadLoading_art: boolean;
  typeaheadNoResults_art: boolean;
  dataSource_art: Observable<any>;

  tipocontrato: any;
  estacionservicio: any;
  name = 'ng2-ckeditor';
  ckeConfig: any;
  mycontent: string;
  newcompo: estacionservicio;
  tipocontratosel: tipocontrato;
  componentesel: Componente;
  actuacionesNew: ComponenteActuacion;
  prioridades: CategoriaIncidente[];
  irespuesta: Respuesta;
  estadoincidente = ESTADOINCIDENCIAS;
  apiurl: any;
  @ViewChild('myckeditor') ckeditor: any;
  @ViewChild('rtackeditor') rtackeditor: any;
  @ViewChild('createSwal') private createSwal: SwalComponent;
  @ViewChild('backwardSwal') private backwardSwal: SwalComponent;

  @ViewChild('partetrabajoSwal') private partetrabajoSwal: SwalComponent;
  articulodesc: any;
  materialnew: MaterialRespuesta;
  comentarionew: ComentarioIncidente;
  respuestaview: Respuesta;
  tecnicoadicionalnew: Tecnicoadicional;
  usuariostec = [];
  constructor(
    private spinnerService: Ng4LoadingSpinnerService,
    private incidenciaService: IncidenciaService,
    private router: Router,
    private notificationService: NotificationService,
    private localDataService: LocalDataService,
    private clienteService: ClienteService,
    private localeService: BsLocaleService,
    private tipocontratoService: TipocontratoService,
    private estacionservicioService: EstacionservicioService,
    private route: ActivatedRoute,
    private respuestaService: RespuestasService,
    private articuloService: ArticuloService,
    private comentarioincidenteService: ComentarioincidenteService

  ) {

    this.localeService.use('es');

    this.dataSource = Observable.create((observer: any) => {
      // Runs on every search
      this.clienteService.gettoken(this.item.razonsocial).subscribe((value) => {
        observer.next(value.data);

      });
    });

    this.dataSource_art = Observable.create((observer: any) => {
      // Runs on every search
      this.articuloService.gettoken(this.articulodesc).subscribe((value) => {
        observer.next(value.data);

      });
    });
    this.apiurl = environment.apiUrl;
    this.tecnicoadicionalnew = new Tecnicoadicional();
    this.tecnicoadicionalnew.id_usuario = -1;
  }

  public hasBaseDropZoneOver: boolean = false;
  public hasAnotherDropZoneOver: boolean = false;
  ngOnInit() {

    this.ckeConfig = {
      allowedContent: false,
      extraPlugins: 'divarea',
      forcePasteAsPlainText: true,
      readOnly: true
    };
    this.item = new Incidencia;
    this.item.cliente_id = '';
    this.item.estacion_servicio_id = -1;
    this.item.categoria_incidente_id = -1;
    this.item.imagenes = [];
    this.cargarTipoCOntrato();
    this.cargarEstacionServicio();
    this.newcompo = new estacionservicio();
    this.prioridades = [];
    this.irespuesta = new Respuesta();
    this.materialnew = new MaterialRespuesta();
    this.irespuesta.materiales = [];
    this.getById();

    this.irespuesta.estadorespuesta = "";
    this.uploader.onAfterAddingFile = (file: any) => { file.withCredentials = false; };
    this.uploader.onErrorItem = (item, response, status, headers) => this.onErrorItem(item, response, status, headers);
    this.uploader.onSuccessItem = (item, response, status, headers) => this.onSuccessItem(item, response, status, headers);
    this.uploader.onWhenAddingFileFailed = (fileItem) => {
      this.notificationService.onError('La imagen no se pudo cargar, '
        + 'el tamao maximo es de 5MB y el formato es png o jpg, INtentelo nuevamente');
    };
    this.comentarionew = new ComentarioIncidente();

  }

  onChange($event: any): void {
    console.log("onChange");

  }
  editarfecha($event) {
    if($event!=undefined){
      this.backwardSwal.show();
    }
    
  }
  getById() {
    this.spinnerService.show();
    const $id = this.route.snapshot.paramMap.get('id');
    this.uploader.options.additionalParameter = { incidencia_id: $id };
    this.irespuesta.id_incidencia = $id;
    this.incidenciaService.getById($id)
      .subscribe((response) => {
        this.item = response.data;
        this.usuariostec = response.data.usuariostec;
        this.cargarEstacionServicio();
        if (response.data.contrato_componente != null) {
          this.prioridades = response.data.contrato_componente.contrato.tipocontrato.categoriaincidentes;
        }
        if (!moment(this.item.fecha_cierre, 'DD/MM/YYYY').isValid()) {
          this.item.fecha_cierre = moment(this.item.fecha_cierre).toDate();
        }

        this.spinnerService.hide();
      });
  }
  cargarTipoCOntrato() {

    this.tipocontratoService.get(1).subscribe((value) => {
      this.tipocontrato = value.data;

    });

  }

  cargarEstacionServicio() {
    let empresa = 0;
    this.spinnerService.show();
    empresa = this.localDataService.getLocalData('empresaSeleccionada').id;
    this.estacionservicioService.getbycliente(empresa, this.item.cliente_id).subscribe((value) => {
      this.estacionservicio = value.data;
      if (this.item.estacion_servicio_id != -1) {
        let result = this.estacionservicio.filter(comp => comp.id.toString() == this.item.estacion_servicio_id);


        if (result.length > 0) {
          this.newcompo = result[0];

        }
      }
      else {
        this.newcompo = new estacionservicio();

      }
      this.spinnerService.hide();
    });

  }

  onselectEstacionServicio(estacionserviciosel) {
    if (estacionserviciosel.target.value != '-1') {
      let result = this.estacionservicio.filter(comp => comp.id.toString() == estacionserviciosel.target.value);


      if (result.length > 0) {
        this.newcompo = result[0];
      }
    }
    else {
      this.newcompo = new estacionservicio();

    }


  }
  onselecttipocontrato(tipocontratovalue) {

    let result;
    result = this.tipocontrato.filter(comp => comp.id.toString() == tipocontratovalue.target.value);


    if (result.length > 0) {
      this.tipocontratosel = result[0];
    }
  }

  add() {
    this.spinnerService.show();

    this.item.usuario_id = this.localDataService.getLocalData('userInfo').id;
    if (!moment(this.item.fecha_cierre, 'DD/MM/YYYY').isValid()) {
      this.item.fecha_cierre = moment(this.item.fecha_cierre).toDate();
    } else {
      this.item.fecha_cierre = moment(this.item.fecha_cierre).toDate();
    }
    if (this.item.fecha_programacion != undefined) {
      this.item.fecha_programacion = moment(this.item.fecha_programacion).format("DD-MM-YYYY HH:mm:ss");

    }
    this.incidenciaService.update(this.item)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Incidencia actualizada con exito.');
        this.router.navigateByUrl('incidencia');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError('El Contrato ya existe');
        } else {
          this.notificationService.onError('Se ha producido un error.');
        }
      });
  }
  changeTypeaheadLoading(e: boolean): void {

    this.typeaheadLoading = e;
  }

  typeaheadOnSelect(e: TypeaheadMatch): void {
    this.item.cliente_id = e.item.CODIGO_CLIENTE;
    this.cargarEstacionServicio();
  }

  changeTypeaheadLoading_art(e: boolean): void {

    this.typeaheadLoading_art = e;
  }

  typeaheadOnSelect_art(e: TypeaheadMatch): void {
    this.materialnew.nombre = e.item.DESCRIPCION_ARTICULO;
    this.materialnew.referencia = e.item.CODIGO_ARTICULO;
  }
  agregarmaterial() {
    this.irespuesta.materiales.push(this.materialnew);
    this.materialnew = new MaterialRespuesta();
    this.articulodesc = "";
  }
  selectComponent(comp) {
    if (comp.select) {
      comp.select = false;
    } else {
      comp.select = true;
    }
    return false;
  }
  modalactuacion(item) {
    this.componentesel = item;
    if (!this.componentesel.actuaciones) {
      this.componentesel.actuaciones = [];
    }

    this.createSwal.show();
    return false;
  }
  agregaractuacion() {
    this.componentesel.actuaciones.push(this.actuacionesNew);
    this.actuacionesNew = new ComponenteActuacion();
    this.actuacionesNew.tipo = '-1';
  }
  onvaluechanged(value) {
    if (value.index === undefined || value.index === '') {
      this.actuacionesNew.dias = value.dias;
    } else {
      this.componentesel.actuaciones[value.index].dias = value.dias;
    }

  }
  clickcontrato(contrato) {
    this.prioridades = contrato.contrato.tipocontrato.categoriaincidentes;
    this.item.contrato_componente_id = contrato.id;
  }
  getclassestadoincidencia(estado) {
    let result;
    result = this.estadoincidente.filter(estadoinc => estadoinc.value === estado);
    return result.length > 0 ? result[0].label : '';
  }
  onErrorItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    let error = JSON.parse(response); //error server response
    console.log(error);
  }

  public fileOverBase(e: any): void {

    this.hasBaseDropZoneOver = e;
  }

  public fileOverAnother(e: any): void {
    this.hasAnotherDropZoneOver = e;
  }
  onSuccessItem(item: FileItem, response: string, status: number, headers: ParsedResponseHeaders): any {
    if (this.item.imagenes == undefined || this.item.imagenes == null) {
      this.item.imagenes = [];
    }
    this.item.imagenes.push($.parseJSON(response).data);
  }
  tooglerogthmenu() {
    $('body').toggleClass('rmv-right-panel');
  }
  guardarrespuesta() {
    this.spinnerService.show();
    this.respuestaService.add(this.irespuesta)
      .subscribe((value) => {
        this.spinnerService.hide();
        this.notificationService.onSuccess('Respuesta agregada con exito.');
        this.router.navigateByUrl('incidencia');
      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError('El Respuesta ya existe');
        } else {
          this.notificationService.onError('Se ha producido un error.');
        }
      });
  }
  abrirfirma() {
    this.createSwal.show();
  }

  onvaluechangedfirma(value) {
    this.irespuesta.firmacliente = value;
    this.createSwal.nativeSwal.close();
  }
  cancelarfirma(value) {
    this.createSwal.nativeSwal.close();
  }
  public ngAfterViewInit() {
    $('.mini-nav, .sidebar-menu, .right-side-panel, .message-center, .right-sidebar').perfectScrollbar();
  }
  FieldsChange(values: any) {
    this.materialnew.pedido = values.currentTarget.checked;
  }
  ocultarmenu() {
    $('body').toggleClass('rmv-right-panel');
  }
  addcomentario() {
    if (this.item.comentarios == undefined) {
      this.item.comentarios = [];
    }
    this.comentarionew.id_incidencia = this.item.id;

    this.comentarioincidenteService.add(this.comentarionew)
      .subscribe((value) => {
        this.item.comentarios.push(value.data);
        this.comentarionew = new ComentarioIncidente();
        this.notificationService.onSuccess('Comentario agregado con exito.');

      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError('Comentario ya existe');
        } else {
          this.notificationService.onError('Se ha producido un error.');
        }
      });
  }
  calculardias(fecha) {
    let actual = moment();
    fecha = moment(fecha);
    let diff = actual.diff(fecha, 'days');
    return diff;
  }
  verparte(ptrab) {
    this.respuestaview = ptrab;
    this.partetrabajoSwal.show();
    return false;
  }
  agregartecnicoadicional() {
    if (this.tecnicoadicionalnew.id_usuario != -1) {
      if (this.item.tecnicosadicionales == undefined) { this.item.tecnicosadicionales = []; }
      let resul = this.usuariostec.filter(tec => tec.id == this.tecnicoadicionalnew.id_usuario);
      resul[0].selected = true;
      this.tecnicoadicionalnew.usuario = resul[0];
      this.item.tecnicosadicionales.push(this.tecnicoadicionalnew);
      this.tecnicoadicionalnew = new Tecnicoadicional();
      this.tecnicoadicionalnew.id_usuario = -1;
    }

  }
}



