import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
/*modules */

import { AuthenticationModule } from './../shared';
import { EnvironmentModule } from './../shared';
import { HttpModule } from '@angular/http';
import { FormValidationModule } from './../shared';
import { NotificationModule } from './../shared';
import { LocalDataModule } from './../shared';
import { AlertModule } from 'ngx-bootstrap/alert';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';
import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';
import { TypeaheadModule } from 'ngx-bootstrap/typeahead';
import { BsDatepickerModule } from 'ngx-bootstrap/datepicker';
import { TimepickerModule } from 'ngx-bootstrap/timepicker';
import * as $ from "jquery";

import { defineLocale } from 'ngx-bootstrap/chronos';
import { esLocale } from 'ngx-bootstrap/locale';
import { CKEditorModule } from 'ng2-ckeditor';
import { DiasWeekModule } from './../dias-week/dias-week.module';

import { IncidenciasRoutingModule } from './incidencias-routing.module';
import { IncidenciaIndexComponent } from './incidencia-index/incidencia-index.component';
import { IncidenciaService } from './incidencia.service';
import { RespuestasService } from './../respuesta/respuestas.service';
import { RequestInterceptor } from '../shared/interceptor/http_interceptor';
import { IncidenciaAddComponent } from './incidencia-add/incidencia-add.component';
import { IncidenciaEditComponent } from './incidencia-edit/incidencia-edit.component';
import { FullCalendarModule } from 'ng-fullcalendar';
import {FileUploadModule} from 'ng2-file-upload';
import { SinguserModule } from '../singuser/singuser.module';
import { ArticuloService } from '../articulo/articulo.service';
import { MapsService } from '../google-apis/maps.service';
import { ComentarioincidenteService } from '../comentarioincidente/comentarioincidente.service';
import { IncidenciasResumenComponent } from './incidencias-resumen/incidencias-resumen.component';
import { Candeactivateguard } from '../shared/can-deactivate-guard/can-deactivate-guard.service';
import { NgSelectModule } from '@ng-select/ng-select';
import { NoDblClickDirectiveDirective } from '../directives/no-dbl-click-directive.directive';
import { IncidenciaEditTabComponent } from './incidencia-edit-tab/incidencia-edit-tab.component';
defineLocale('es', esLocale);
@NgModule({
  imports: [
    CommonModule,
    IncidenciasRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    BsDatepickerModule.forRoot(),
    TimepickerModule.forRoot(),
    AlertModule.forRoot(),
    SweetAlert2Module.forRoot(),
    SnotifyModule,
    HttpModule,
    AuthenticationModule,
    EnvironmentModule,
    FormValidationModule,
    NotificationModule,
    LocalDataModule,
    TypeaheadModule.forRoot(),
    BsDatepickerModule.forRoot(),

    CKEditorModule,
    DiasWeekModule,
    FullCalendarModule,
    FileUploadModule,
    SinguserModule,
    NgSelectModule,

  ],
  declarations: [IncidenciaIndexComponent, IncidenciaAddComponent, IncidenciaEditComponent,
    IncidenciasResumenComponent,
  NoDblClickDirectiveDirective,
  IncidenciaEditTabComponent],
  providers: [IncidenciaService,RespuestasService,
    ArticuloService,MapsService,ComentarioincidenteService
    ,Candeactivateguard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: RequestInterceptor,
      multi: true,
    }]
})
export class IncidenciasModule {

}



